import React from 'react';

const CloseIcon = () => 
  <svg viewBox="0 0 32 32">
    <rect height="30" width="30" x="1" y="1" />
    <line x1="10" x2="22" y1="10" y2="22" />
    <line x1="22" x2="10" y1="10" y2="22" />
  </svg>

export default CloseIcon;


